#header{
    position: absolute;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-sizing: border-box;
    padding: 21px 4%;
    
    .logo{
        width:91.04px;
        height:41px;
    }
    .menu{
        width:20px;
        height:13.33px;
    }
}

@media screen and (min-width: 768px) {
    #header{
        padding: 21px 7.16%;
    }
}

