.home{
    background-image: url('../../styles/assets/backy.jpeg');
    height: 98vh;
    background-size: cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 0 4.34%;
    box-sizing: border-box;
    background-position: -522px center;
}

.headline{
    font-family: poppins;
    font-weight:900;
    font-size: 34px;
    color:white;
    text-align: left;
    line-height:44.2px;
    letter-spacing:-2px;
    margin-bottom:36px;
}

.sub-headline{
    font-family: muli;
    font-weight:600;
    font-size: 18px;
    color: white;
    text-align: left;
    line-height:23.2px;
    letter-spacing:-.2px;
}

@media screen and (min-width: 768px) {
    .home{
        padding: 0 7.16%;
    }
}